.contact-main-wrapper {
  display: inline-flex;
  align-items: center;
  /* justify-content: space-between; */
  border: 1px solid rgb(225, 225, 225);
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 6px 14px -6px rgb(24 39 75 / 12%),
    0px 10px 32px -4px rgb(24 39 75 / 10%),
    inset 0px 0px 2px 1px rgb(24 39 75 / 5%);
}
.contact-text {
  font-size: 23px;
}
.contact-text-wrapper {
  font-size: 23px;
  margin-top: 25px;
  margin-bottom: 25px;
}
i.contact-last-updated-on {
  font-size: 12px;
  color: rgb(65, 65, 65);
}
.contact-name {
  font-size: 2.5rem;
  font-weight: bold;
}
.contact-title {
  font-size: 20px;
  color: #006aff;
  font-weight: 700;
}

/* i.contact-last-updated-on-white {
  font-size: 12px;
  color: rgb(255, 255, 255);
} */
i.contact-last-updated-on-opacity-0 {
  font-size: 12px;

  opacity: 0;
}
.contact-luo {
  display: block;
}
.contact-button-container {
  display: inline-flex;
}
.contact-buttons {
  margin-right: 15px;
}
.contact-profile-img {
  width: 200px;
  border-radius: 50%;
  border: 1px solid #d9d9d9;
  margin-right: 20px;
  box-shadow: 0px 6px 14px -6px rgb(24 39 75 / 12%),
    0px 10px 32px -4px rgb(24 39 75 / 10%),
    inset 0px 0px 2px 1px rgb(24 39 75 / 5%);
}
/* Media Query for responsiveness */
@media only screen and (max-width: 745px) {
  .contact-profile-img {
    width: 185px;
    margin-top: 25px;
  }
  .contact-main-wrapper {
    display: block;
    text-align: center;
  }
  .contact-profile-img-wrapper {
    text-align: center;
  }
  .contact-text-wrapper {
    text-align: center;
  }
}
@media only screen and (max-width: 600px) {
  .contact-text {
    font-size: 20px;
  }
}
@media only screen and (max-width: 434px) {
  .contact-button-container {
    display: block;
  }
  .contact-name {
    font-size: 2.25rem;
    font-weight: bold;
  }
  .contact-title {
    font-size: 18px;
    color: #006aff;
    font-weight: 700;
  }
}
